import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Container, Row, Col, Accordion, Dropdown, Card, Button, Badge } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import '../App.css';

const Movies = () => {

    const { popularMovies } = useSelector(state => state.movie)
    const selectedmovie = useSelector(state => state.movie.keyword)
    const [filter, setFilter] = useState([])

    //    redux   
    const { genreList } = useSelector(state => state.movie)


    /*  
         sort
        -     
        - compare   
        
        .sort((a,b) => {
            return a-b; -> 
            return b-a; -> 
        });

        []
        .localeCompare( )

         < 0 : a b  
         == 0 :  X
         > 0 : b a  
    */

    const movieSorted = (category, sortMethod) => {

        // 1.   
        // 2.   
        // onClick  onClick()=>{movieSorted('', 'ASC')}

        let sortedMovies = [...filter]; //    

        if (category === '') {
            sortMethod === 'ASC'
                ? filter.sort((a, b) => a.vote_average - b.vote_average)
                : filter.sort((a, b) => b.vote_average - a.vote_average)
        } else if (category === '') {
            sortMethod === 'ASC'
                ? filter.sort((a, b) => a.title.localeCompare(b.title))
                : filter.sort((a, b) => b.title.localeCompare(a.title))
        } else if (category === '') {
            sortMethod === 'ASC'
                ? filter.sort((a, b) => a.popularity - b.popularity)
                : filter.sort((a, b) => b.popularity - a.popularity)
        }

        setFilter(sortedMovies); //     
    }

    const handleSort = (type) => {
        let sortedMovies = [...filter]; //    

        if (type === 'titleAsc') {
            sortedMovies.sort((a, b) => a.title.localeCompare(b.title)); //  
        } else if (type === 'titleDesc') {
            sortedMovies.sort((a, b) => b.title.localeCompare(a.title)); //  
        } else if (type === 'voteAverageAsc') {
            sortedMovies.sort((a, b) => a.vote_average - b.vote_average); //   ( )
        } else if (type === 'voteAverageDesc') {
            sortedMovies.sort((a, b) => b.vote_average - a.vote_average); //   ( )
        } else if (type === 'popularityAsc') {
            sortedMovies.sort((a, b) => a.popularity - b.popularity); //   ( )
        } else if (type === 'popularityDesc') {
            sortedMovies.sort((a, b) => b.popularity - a.popularity); //   ( )
        }

        setFilter(sortedMovies); //     
    };

    useEffect(() => {
        // console.log('popularMovies', popularMovies);

        if (popularMovies.length !== 0) {
            setFilter(popularMovies)
        }
    }, [])

    return (
        <div>
            {popularMovies.length > 0 && (
                <>
                    <Container>
                        <Row>
                            <Col>
                                < h1> </h1>
                            </Col>
                        </Row>
                        <Row>
                            <Col sm={3}>
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header></Accordion.Header>
                                        <Accordion.Body>
                                            <Dropdown>

                                                <Dropdown.Toggle variant="secondary" id="dropdown-basic">
                                                      .
                                                </Dropdown.Toggle>

                                                <Dropdown.Menu>
                                                    <Dropdown.Item onClick={() => handleSort('titleAsc')}> </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => handleSort('titleDesc')}> </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => handleSort('voteAverageAsc')}> </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => handleSort('voteAverageDesc')}> </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => handleSort('popularityAsc')}> </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => handleSort('popularityDesc')}> </Dropdown.Item>
                                                </Dropdown.Menu>

                                            </Dropdown>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col sm={9} className='movie-card-list'>
                                {filter.filter(item => item.title.includes(selectedmovie)).map(item => (
                                    <>
                                        <Card style={{ width: '13rem' }} key={item.id}>
                                            <div className='popular-movie-card'>
                                                <Card.Img variant="top" src={`https://www.themoviedb.org/t/p/w220_and_h330_face${item.poster_path}`} />
                                                <Link to={`/movie/${item.id}`}>
                                                    <div className='popular-overlay'>
                                                        <h1>{item.title}</h1>
                                                        <div className='popular-genres'>
                                                            {item.genre_ids.map(id => (
                                                                <Badge bg="danger" key={id}>{genreList.find(item => item.id === id).name}</Badge>
                                                            ))}
                                                        </div>
                                                        <div className='info'>
                                                            <span> : {Math.round(item.vote_average * 10) / 10}</span>
                                                            <span>|</span>
                                                            <span>{item.adult ? ' ' : ' '}</span>
                                                        </div>
                                                    </div>
                                                </Link>
                                            </div>
                                            <Card.Body>
                                                <Card.Title>{item.title}</Card.Title>
                                                <Card.Text>
                                                     : {item.release_date}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    </>
                                ))}
                            </Col>
                        </Row>
                    </Container>
                </>
            )}
        </div>
    )
}

export default Movies